:root {
    --te-bg-color: rgb(30, 35, 60);
}

.today-weather-info-wrapper {
    background-image: url("../assets/Cloudy.png");
    background-size: 48%;
}

.today-weather-wrapper {
    background-color: var(--te-bg-color);
    width: 30%;
    text-align: center;
    color: white;
    padding: 30px;  
}


.today-weather-search-loc-wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.tw-close-wrapper {
   width: 100%;
    margin-top: 5px;
    margin-bottom: 20px;
    display: flex;
    justify-content: flex-end;
}

.tw-search-btn-x {

    background-color: rgba(1, 1, 1, 0);
    border: none;
    color: white;
}

.today-weather-btn {
    background-color: rgb(172, 175, 192);
    border: 1px solid rgb(54, 56, 61);
    color: white;
}

.today-weather-gps-btn {
    background-color: rgb(1, 1, 1,0);
    border-radius: 50%;
    text-align: center;
    cursor: pointer;
}

.today-weather-temperature-number {
    font-size: xxx-large;
}

.today-weather-image {
    height: 150px;
}

.today-weather-temperature-number {
    font-size: 5em;
}

.today-weather-temperature-sign {
    font-size: 2em;
}

.today-weather-climate{
    font-size: 1em;
}

.today-weather-time-date {
    font-size: 0.5em;
}

.today-weather-location-div {
    display: flex; 
    justify-content: center;
    align-items: center;
}

/* search view */

.tw-searchbar-btn {
    display: flex;
}

.tw-search-div select {
    width: 100%;
}

.tw-search-div input {
    width: 80%;
}

.tw-search-div input, .today-weather-wrapper select, .tw-searchbar-btn button{
    margin-top: 40px;
    padding: 10px;
    margin-right: 3px;
    border: 1px solid  #fefefe;
    background: var(--te-bg-color);
    font-size: 1em;
    color: #fefefe;
}

.tw-search-div button {
    background-color: rgb(60, 70, 230);
    color: white;
}

.timeicon-img {
    width: 40%;
}
